.usernav {
    margin-top: 1em;
}

body {
    @include grid-row;
    > nav {
        @include grid-column;
        @include breakpoint(medium) {
            //min-width: 200px;
            @include grid-column(22%);
        }

        [type="text"] {
            width: 0;
        }
        form {
            margin-top: 0.5em;
        }


        > h5 {
            font-weight: lighter;
        }
        > header {
            font-size: 3rem;
        }
        ul {
            @include menu-base;
            @include menu-expand;
            @include menu-direction(vertical);
            li a:hover {
                background-color: lighten($body-background, 10%);
            }
        }
        fieldset.fieldset {
            margin: 0;
            padding: 0;
        }
        .usernav > fieldset {
            border: 0;
            legend {
                background-color: unset;
            }
        } 
    }
    > section {
        @include grid-column;
        @include breakpoint(medium) {
            @include grid-column(78%);
        }
        > main {
            article {
                @include card();
                padding: 0.5em;

                footer {
                    text-align: right;
                    ul {
                        list-style: none;
                    }
                }

                ul {
                    list-style-type: disc;

                    li header {
                        font-size: 2rem;
                    }
                }
                pre,code {
                    white-space: pre-wrap;
                    background: darken($solarized, 10%);
                    color: $white;
                }
                code {
                    padding: 0;
                }
                pre {
                    padding: 0.5rem;
                }
                input[type=checkbox] { display:none; } /* to hide the checkbox itself */
                input[type=checkbox] + label:before {
                    font-family: FontAwesome;
                    display: inline-block;
                }

                input[type=checkbox] + label {
                    margin-left: 0;
                    margin-right: 0;
                }
                input[type=checkbox] + label[for] {
                    cursor: default;
                }

                input[type=checkbox] + label:before { content: "\f096"; } /* unchecked icon */
                input[type=checkbox] + label:before { letter-spacing: 10px; } /* space between checkbox and label */

                input[type=checkbox]:checked + label:before { content: "\f046"; } /* checked icon */
                input[type=checkbox]:checked + label:before { letter-spacing: 5px; } /* allow space for check mark */
            }
            header {
                @include card();
                display: flex;
                flex-flow: row nowrap;

                margin-bottom: 1em;
                > h1 {
                    flex-grow: 1;
                    font-size: 2.6rem;
                }
                #stats-dropdown {
                    width: auto;
                    background-color: $almostblack;
                }
                div.stats {
                    flex-shrink: 1;
                    font-family: $header-font-family;
                    font-weight: lighter;

                    ul {
                        margin: 0;
                    }
                    li {
                        display: inline-block;
                        border-right: 1px solid transparentize($body-font-color, 0.8);
                        padding: 0.2em;
                        height: 3.8em;
                        p {
                            margin-bottom: 1rem;
                        }
                        white-space: nowrap;
                    }
                    li:first-child {
                        border-left: 1px solid transparentize($body-font-color, 0.8);
                    }
                    .stat {
                        font-size: 1.0rem;
                        font-weight: bold;
                    }
                    .stat span.warning.label {
                        margin-right: 0.2em;
                        margin-left: 0.2em;
                    }
                }                
            }
        }
    }
}
header {
    //font-family: 'Khand', sans-serif;
    font-weight: bold;
    //font-size: 3rem;
    font-family: $header-font-family;
    text-decoration: none;
    //border-bottom: $card-border;
    //padding-left: 0.2em;
    & a, a:hover, a:visited {
        color: $white;
        text-decoration: none;
    }
}


.switch-paddle {
    width: 3rem;
    height: 1.5rem;
    font-size: .625rem;
}

.switch-paddle::after {
    width: 1rem;
    height: 1rem;
}

input:checked ~ .switch-paddle::after  {
    left: 1.75rem;
}

#uncheckedSwitch,#checkedSwitch {
    display: none;
}


#gallery img {
    max-width: 400px;
}

.accordion-title {
    font-size: 1rem;
    &:hover, &:focus, &{
        background-color: $white;
        color: $black;
    }
}

code {
    border: 0;
}

p {
    //font-weight: lighter;
    //font-family: 'Roboto Condensed', sans-serif;
    margin-bottom: 0;
}

footer {
    text-align: center;
    color: transparentize($body-font-color, 0.6);
    font-weight: lighter;
    font-size: 70%;
    > ul {
        list-style: none;
        margin: 0;
    }
}

i.fa:after {
    content: "\00a0";
}

p.date {
    color: transparentize($body-font-color, 0.6);
    font-size: 0.8rem;
    margin-bottom: 0.5em;
    > span + span:before {
        content: "|\00a0";
        //padding: 0 1em;
    }
}

ul.tabs {
    border: 0;
    background-color: unset;
    white-space: nowrap;
    > li {
        background-color: $black;
        margin-right: 0.5em;
        width: 100%;
        @include breakpoint(640px) {
            width: auto;
        }
    }
    > li.is-active {
        background-color: $almostblack;
        a {
            color: $white;
        }
        &:hover {
            background-color: $almostblack;
        }
    }
}

.accordion-title {
    font-size: 1rem;
    &:hover, &:focus, &{
        background-color: $white;
        color: $black;
    }
}

div.input-wrapper {
    box-sizing: border-box;
    display: flex;
    flex-flow: row nowrap;

    /* Example styling ------------------- */
    //padding: 0 20px;
    //margin-top: 10px;
    height: 2.2em;
        
    & > input, & > button {
      box-sizing: border-box;
      height: 100%;
      border: 1px solid #999;
    }

    & > input {
      flex: 1 1 auto;
      border-right: 0;
      border-radius: 0;
    }

    & > button {
      flex-shrink: 0;
      margin: 0;
      border-radius: 0;
    }
}

#diff pre {
    background-color: $solarized;
    code {
        background-color: $solarized;
    }
}

main > article > nav {
    > ul {
        margin: 1.5rem;
    }
    border: 1px solid transparentize($body-font-color, 0.8);
    display: table;
    background-color: $white;
}

div.usernav {
    background-color: darken($body-background, 10%);
}

.tabs-title > a:hover {
    background-color: $almostblack;
}

textarea#wikieditor {
  height: 500px;
}